          <el-form :model="form" label-width="70px">
            <el-form-item label="ÂÜÖÂÆπÁ±ªÂûã">
              <el-select style="width: 100%" v-model="form.type" @change="handleTypeChange">
                <el-option label="ÊñáÊú¨" value="text"></el-option>
                <el-option label="ÁΩëÂùÄ" value="url"></el-option>
                <el-option label="WiFi" value="wifi"></el-option>
                <el-option label="ÁîµËØù" value="tel"></el-option>
                <el-option label="Áü≠‰ø°" value="sms"></el-option>
                <el-option label="ÈÇÆÁÆ±" value="email"></el-option>
                <el-option label="ÂêçÁâá" value="vcard"></el-option>
              </el-select>
            </el-form-item>

            <!-- ÊñáÊú¨ËæìÂÖ• -->
            <el-form-item label="ÂÜÖÂÆπ" v-if="form.type === 'text'">
              <el-input
                type="textarea"
                v-model="form.content"
                placeholder="ËØ∑ËæìÂÖ•Ë¶ÅÁîüÊàê‰∫åÁª¥Á†ÅÁöÑÊñáÊú¨ÂÜÖÂÆπ"
                :autosize="{ minRows: 4, maxRows: 10 }"
                @input="generateQrCode"
              ></el-input>
            </el-form-item>

            <!-- ÁΩëÂùÄËæìÂÖ• -->
            <el-form-item label="ÁΩëÂùÄ" v-if="form.type === 'url'">
              <el-input
                v-model="form.url"
                placeholder="https://example.com"
                @input="generateQrCode"
              ></el-input>
            </el-form-item>

            <!-- WiFiËÆæÁΩÆ -->
            <template v-if="form.type === 'wifi'">
              <el-form-item label="ÁΩëÁªúÂêçÁß∞">
                <el-input v-model="form.wifi.ssid" placeholder="WiFiÂêçÁß∞" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="ÂØÜÁ†Å">
                <el-input v-model="form.wifi.password" placeholder="WiFiÂØÜÁ†Å" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="Âä†ÂØÜÁ±ªÂûã">
                <el-select v-model="form.wifi.security" @change="generateQrCode">
                  <el-option label="WPA/WPA2" value="WPA"></el-option>
                  <el-option label="WEP" value="WEP"></el-option>
                  <el-option label="Êó†ÂØÜÁ†Å" value="nopass"></el-option>
                </el-select>
              </el-form-item>
            </template>

            <!-- ÁîµËØù -->
            <el-form-item label="ÁîµËØùÂè∑Á†Å" v-if="form.type === 'tel'">
              <el-input v-model="form.phone" placeholder="+86 138 0013 8000" @input="generateQrCode"></el-input>
            </el-form-item>

            <!-- Áü≠‰ø° -->
            <template v-if="form.type === 'sms'">
              <el-form-item label="ÊâãÊú∫Âè∑">
                <el-input v-model="form.sms.phone" placeholder="ÊâãÊú∫Âè∑Á†Å" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="Áü≠‰ø°ÂÜÖÂÆπ">
                <el-input v-model="form.sms.message" placeholder="Áü≠‰ø°ÂÜÖÂÆπ" @input="generateQrCode"></el-input>
              </el-form-item>
            </template>

            <!-- ÈÇÆÁÆ± -->
            <template v-if="form.type === 'email'">
              <el-form-item label="ÈÇÆÁÆ±Âú∞ÂùÄ">
                <el-input v-model="form.email.address" placeholder="example@email.com" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="‰∏ªÈ¢ò">
                <el-input v-model="form.email.subject" placeholder="ÈÇÆ‰ª∂‰∏ªÈ¢ò" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="ÂÜÖÂÆπ">
                <el-input v-model="form.email.body" type="textarea" placeholder="ÈÇÆ‰ª∂ÂÜÖÂÆπ" @input="generateQrCode"></el-input>
              </el-form-item>
            </template>

            <!-- ÂêçÁâá -->
            <template v-if="form.type === 'vcard'">
              <el-form-item label="ÂßìÂêç">
                <el-input v-model="form.vcard.name" placeholder="ÂßìÂêç" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="ÂÖ¨Âè∏">
                <el-input v-model="form.vcard.org" placeholder="ÂÖ¨Âè∏ÂêçÁß∞" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="ÁîµËØù">
                <el-input v-model="form.vcard.phone" placeholder="ÁîµËØùÂè∑Á†Å" @input="generateQrCode"></el-input>
              </el-form-item>
              <el-form-item label="ÈÇÆÁÆ±">
                <el-input v-model="form.vcard.email" placeholder="ÈÇÆÁÆ±Âú∞ÂùÄ" @input="generateQrCode"></el-input>
              </el-form-item>
            </template>

            <el-divider>Ê†∑ÂºèËÆæÁΩÆ</el-divider>

            <el-form-item label="Â∞∫ÂØ∏">
              <el-slider v-model="form.size" :min="100" :max="500" :step="10" @change="generateQrCode"></el-slider>
              <span>{{ form.size }}px</span>
            </el-form-item>

            <el-form-item label="ÂÆπÈîôÁ∫ßÂà´">
              <el-select v-model="form.errorLevel" @change="generateQrCode">
                <el-option label="‰Ωé (7%)" value="L"></el-option>
                <el-option label="‰∏≠ (15%)" value="M"></el-option>
                <el-option label="È´ò (25%)" value="Q"></el-option>
                <el-option label="ÊúÄÈ´ò (30%)" value="H"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="ÂâçÊôØËâ≤">
              <el-color-picker v-model="form.foreground" @change="generateQrCode"></el-color-picker>
            </el-form-item>

            <el-form-item label="ËÉåÊôØËâ≤">
              <el-color-picker v-model="form.background" @change="generateQrCode"></el-color-picker>
            </el-form-item>
          </el-form>
        </el-card>
      </el-col>

      <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12" class="preview-col" :class="{ 'mobile-hidden': isMobile && activeView !== 'preview' }">
        <el-card class="preview-card">
          <div slot="header">
            <span>‰∫åÁª¥Á†ÅÈ¢ÑËßà</span>
          </div>
          
          <div class="qr-preview">
            <canvas ref="qrCanvas" :width="form.size" :height="form.size"></canvas>
          </div>

          <div class="button-group">
            <el-button type="primary" @click="downloadQrCode">
              <i class="el-icon-download"></i> ‰∏ãËΩΩPNG
            </el-button>
            <el-button @click="downloadSvg">
              <i class="el-icon-document"></i> ‰∏ãËΩΩSVG
            </el-button>
            <el-button @click="copyQrCode">
              <i class="el-icon-copy-document"></i> Â§çÂà∂ÂõæÁâá
            </el-button>
          </div>

          <el-divider>ÁîüÊàêÁöÑÂÜÖÂÆπ</el-divider>
          <el-input
            type="textarea"
            :value="generatedContent"
            readonly
            :rows="6"
            placeholder="ÁîüÊàêÁöÑ‰∫åÁª¥Á†ÅÂÜÖÂÆπÂ∞ÜÊòæÁ§∫Âú®ËøôÈáå"
          ></el-input>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import QRCode from 'qrcode'
import { ClipboardUtil } from '@/util/ClipboardUtil'

export default {
  name: 'QrGeneratorView',
  metaInfo: {
    title: '‰∫åÁª¥Á†ÅÁîüÊàêÂô® - Âú®Á∫øÁîüÊàê‰∫åÁª¥Á†Å',
    meta: [
      { name: 'description', content: 'Âú®Á∫ø‰∫åÁª¥Á†ÅÁîüÊàêÂô®ÔºåÊîØÊåÅÊñáÊú¨„ÄÅÁΩëÂùÄ„ÄÅWiFi„ÄÅÁîµËØù„ÄÅÁü≠‰ø°„ÄÅÈÇÆÁÆ±„ÄÅÂêçÁâáÁ≠âÂ§öÁßçÁ±ªÂûãÁöÑ‰∫åÁª¥Á†ÅÁîüÊàê„ÄÇ' },
      { name: 'keywords', content: '‰∫åÁª¥Á†ÅÁîüÊàêÂô®,QRÁ†Å,Âú®Á∫øÁîüÊàê,WiFi‰∫åÁª¥Á†Å,ÂêçÁâá‰∫åÁª¥Á†Å' }
    ]
  },
  data() {
    return {
      form: {
        type: 'text',
        content: 'Ê¨¢Ëøé‰ΩøÁî®‰∫åÁª¥Á†ÅÁîüÊàêÂô®ÔºÅ',
        url: '',
        phone: '',
        wifi: {
          ssid: '',
          password: '',
          security: 'WPA'
        },
        sms: {
          phone: '',
          message: ''
        },
        email: {
          address: '',
          subject: '',
          body: ''
        },
        vcard: {
          name: '',
          org: '',
          phone: '',
          email: ''
        },
        size: 200,
        errorLevel: 'M',
        foreground: '#000000',
        background: '#FFFFFF'
      },
      generatedContent: '',
      isMobile: false,
      activeView: 'settings'
    }
  },
  mounted() {
    this.generateQrCode()
    this.checkMobile()
    window.addEventListener('resize', this.checkMobile)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.checkMobile)
  },
  methods: {
    checkMobile() {
      this.isMobile = window.innerWidth <= 768
    },

    handleViewChange() {
      // ÁßªÂä®Á´ØËßÜÂõæÂàáÊç¢Êó∂ÁöÑÂ§ÑÁêÜ
    },

    handleTypeChange() {
      this.generateQrCode()
    },

    getQrContent() {
      switch (this.form.type) {
        case 'text':
          return this.form.content
        case 'url':
          return this.form.url
        case 'wifi':
          return `WIFI:T:${this.form.wifi.security};S:${this.form.wifi.ssid};P:${this.form.wifi.password};;`
        case 'tel':
          return `tel:${this.form.phone}`
        case 'sms':
          return `sms:${this.form.sms.phone}?body=${encodeURIComponent(this.form.sms.message)}`
        case 'email':
          return `mailto:${this.form.email.address}?subject=${encodeURIComponent(this.form.email.subject)}&body=${encodeURIComponent(this.form.email.body)}`
        case 'vcard':
          return `BEGIN:VCARD
VERSION:3.0
FN:${this.form.vcard.name}
ORG:${this.form.vcard.org}
TEL:${this.form.vcard.phone}
EMAIL:${this.form.vcard.email}
END:VCARD`
        default:
          return ''
      }
    },

    async generateQrCode() {
      const content = this.getQrContent()
      this.generatedContent = content

      if (!content) return

      try {
        const canvas = this.$refs.qrCanvas
        await QRCode.toCanvas(canvas, content, {
          width: this.form.size,
          errorCorrectionLevel: this.form.errorLevel,
          color: {
            dark: this.form.foreground,
            light: this.form.background
          }
        })
      } catch (error) {
        this.$message.error('ÁîüÊàê‰∫åÁª¥Á†ÅÂ§±Ë¥•Ôºö' + error.message)
      }
    },

    downloadQrCode() {
      const canvas = this.$refs.qrCanvas
      const link = document.createElement('a')
      link.download = 'qrcode.png'
      link.href = canvas.toDataURL()
      link.click()
      this.$message.success('‰∫åÁª¥Á†Å‰∏ãËΩΩÊàêÂäü')
    },

    async downloadSvg() {
      const content = this.getQrContent()
      if (!content) return

      try {
        const svgString = await QRCode.toString(content, {
          type: 'svg',
          width: this.form.size,
          errorCorrectionLevel: this.form.errorLevel,
          color: {
            dark: this.form.foreground,
            light: this.form.background
          }
        })

        const blob = new Blob([svgString], { type: 'image/svg+xml' })
        const url = URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.download = 'qrcode.svg'
        link.href = url
        link.click()
        URL.revokeObjectURL(url)
        this.$message.success('SVGÊñá‰ª∂‰∏ãËΩΩÊàêÂäü')
      } catch (error) {
        this.$message.error('‰∏ãËΩΩÂ§±Ë¥•Ôºö' + error.message)
      }
    },

    async copyQrCode() {
      try {
        const canvas = this.$refs.qrCanvas
        const success = await ClipboardUtil.copyImage(canvas)
        if (success) {
          this.$message.success('‰∫åÁª¥Á†ÅÂ∑≤Â§çÂà∂Âà∞Ââ™Ë¥¥Êùø')
        } else {
          this.$message.error('Â§çÂà∂Â§±Ë¥•')
        }
      } catch (error) {
        this.$message.error('Â§çÂà∂Â§±Ë¥•Ôºö' + error.message)
      }
    }
  }
}
</script>

